import { Button, Card, ProgressBar, Stack } from "react-bootstrap";
import Currency from 'react-currency-formatter';
import { useCurrencies } from "../contexts/CurrencyContext";


export default function CategoryCard({name, amount, max, gray, hideButtons, onAddExpenseClick, onViewExpensesClick}) {

    const {savedCurrency}  = useCurrencies()

    const classNames = []
    if(amount > max){
        classNames.push("bg-danger", "bg-opacity-10")
    } else if(gray){
        classNames.push("bg-light")
    }

  return (
    <Card className={classNames.join(" ")}>
        <Card.Body>
            <Card.Title className="d-flex justify-content-between align-items-baseline fw-normal mb-3">
                <div className="me-2">{name}</div>
                <div className="d-flex align-items-baseline">
                <Currency quantity={amount} currency={savedCurrency}/>
                    {max && (
                        <span className="text-muted fs-6 ms-1"> 
                            / <Currency quantity={max} currency={savedCurrency}/>
                        </span>
                    )}
                </div>
            </Card.Title>
            {max && (<ProgressBar 
                className="rounded-pill" 
                variant={getProgressBarVariant(amount,max)}
                min={0}
                max={max}
                now={amount}
            />)}
            {!hideButtons && <Stack direction="horizontal" gap="2" className="mt-4">
                <Button variant="outline-primary" className="ms-auto" onClick={onAddExpenseClick}>Add Expense</Button>
                <Button onClick={onViewExpensesClick} variant="outline-secondary">View Expenses</Button>
            </Stack>}
        </Card.Body>
    </Card>
  )
}

function getProgressBarVariant(amount,max){
    const ratio = amount/max
    if (ratio < .5) return "primary"
    if (ratio < .75) return "warning"
    return "danger"
}
